@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Bitter:ital,wght@1,600&family=Proza+Libre:wght@500&display=swap');
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header img {
  width: 30px;
}

.hi{
  margin-right: 700px;
  font-family: 'Proza Libre', sans-serif;
}

.ki{
  font-family: 'Bitter', serif;
  
}
.pi{
  margin-right: 1200px;
}

.qi{
  margin-left: 600px;
  
}

.todo-list-item{
  display: flex;
  background-color: antiquewhite;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 3px 5px rgb(43,42,42) ;
  padding: 25px;
  padding-bottom: 10px;
  padding-top: 10px;
  margin-bottom: 10px;
}

.todobtn{
  background-color: rgb(71, 71, 71);
  color: white;
  border: none;
  border-radius: 0px;
  margin-top: 25px;
  padding: 10px;
  width: fit-content;
  cursor: pointer;
}

.active{
  background-color: rgb(99 102 241);
}


